<template>
  <div
      :class="['row-body',{'active-context' : $grid.getters['context'](grid).row == rowKey || mouseOnRow }]"
      :style="[{'grid-template-columns' : virtualHeader.treeWidth.root},{width : virtualHeader.scrollWidth + 'px'}]"
      @click="showContext"
      v-on:mouseover="mouseOnRow = true"
      v-on:mouseout= "mouseOnRow = false"
  >
    <div class="column-body">
        <span class="icon" v-if="emptyContext">
          <i class="el-icon-s-unfold"/>
        </span>
    </div>

    <Column
        v-for="columnKey in virtualHeader.tree.root"
        :item="row[columnKey]"
        :columnKey="columnKey"
        :key="columnKey"
        :grid="grid" />

  </div>
</template>
<script>
import Column from './column'
export default {
  name : 'Row',
  props : ['grid','row','rowKey'],
  components : {Column},
  data() {
    return { mouseOnRow : false }
  },
  computed : {
    virtualHeader : function(){
      return this.$grid.getters['virtualHeader'](this.grid);
    },
    context : function(){
      return this.$grid.getters["context"](this.grid).row == this.rowKey ? true : false;
    },
    emptyContext : function(){
      return Object.keys(this.$grid.getters["context"](this.grid).menu).length > 0 ? true : false;
    },
  },
  methods : {
    showContext : function(e){
      if (e.target.nodeName === 'A') return
      this.$grid.commit('showContext',{name : this.grid,row : this.rowKey})
      this.$nextTick(() => {
        let clientClick = {
              x : e.clientX,
              y : e.clientY,
            },
            windowSize = {
              width : document.documentElement.clientWidth ,
              high  : document.documentElement.clientHeight,
            },
            menu = document.querySelector('.context-menu-wrapper'),
            menuSize = {
              width : menu.clientWidth,
              high  : menu.clientHeight,
            },
            menuOffSet = {
              x : menu.getBoundingClientRect().x,
              y : menu.getBoundingClientRect().y,
            };
        if (clientClick.x + menuSize.width > windowSize.width - 60)
          menu.style.left = clientClick.x - menuSize.width - menuOffSet.x + 'px' ;
        else
          menu.style.left = clientClick.x - menuOffSet.x + 'px';

        if (clientClick.y  + menuSize.high > windowSize.high)
          menu.style.top  = clientClick.y  - menuSize.high - menuOffSet.y + 'px' ;
        else
          menu.style.top  = clientClick.y  - menuOffSet.y + 'px';
      });
    }
  }
}
</script>