<template>
  <div class="profile-wrapper">
    <div class="filter-row">
      <label class="label-filter">Выбор профиля</label>
      <div
          class="filter-fields"
          v-for="(profile, i) in filterData.data.filter.profiles"
          :key="i"
      >
        <el-button
            :class="['change-profile-fields', {'change-fields-input-selected' : i==active }]"
            :icon="i===active ? 'el-icon-check' : ''"
            @click="selectProfile(profile.data, i)" >
          {{profile.name}}
        </el-button>
        <el-button
            v-if="i!==0"
            :class="'profile-button'"
            icon="el-icon-minus"
            @click="removeProfile(i)" />
      </div>
    </div>

    <div class="filter-row" v-if="active === 0">
      <label class="label-filter">Добавление персонального профиля</label>

      <div class="filter-fields">
        <el-button
            v-if="!isSave"
            :class="'profile-button'"
            icon="el-icon-plus"
            @click="onSave">
          Создать свой профиль
        </el-button>
      </div>

      <div v-if="isSave">
        <div class="filter-fields">
          <el-input placeholder="Введите название профиля" v-model="profileName" :class="'change-fields-input'"></el-input>
        </div>
        <div class="filter-fields" >
          <el-button :class="'profile-button'" icon="el-icon-check" @click="addProfile">Сохранить</el-button>
          <el-button :class="'profile-button'" icon="el-icon-close" @click="isSave = false">Отменить </el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

export default {
  name  : "Profiles",
  props : ['grid'],
  data(){
    return {
      filterData     : {
        name : this.grid,
        data : {
          filter : {
            profiles : [],
          },
        },
      },  // объект фильтра для штатной нормализации фильтра у грида
      active         : 0,      // текущий выбранный профиль
      profileName    : '',
      isSave         : false,   // флаг того что пользователь хочет создать свой профиль
      defaultFilds   : [],      // имена полей которые видны у фильтра по умолчанию указаны в приложении у грида
    }
  },
  computed : {
    filter : function(){
      return this.$grid.getters['filter'](this.grid);
    }
  },
  methods : {
    onSave(){
      this.isSave = true;
    },

    selectProfile : function(profile, i){
      // сделать все поля у выбранного профиля видимыми (для дефолтного вернутся свои дефолтовые поля которые заданы в приложении у грида )
      if (i !== 0) {
        for (let key in profile) profile[key].show = true;
      } else {
        for (let key in profile) {
          if (this.defaultFilds.indexOf(key) > 0)  profile[key].show = true; else profile[key].show = false;
        }
      }

      // применить профиль
      Vue.set(this.filterData.data.filter, 'data', profile);
      this.$grid.commit('normalizeFilter', this.filterData);
      this.active = i;
    },

    addProfile: function(){
      if (!this.profileName) return;  // если имя профиля не ввели то нечего сохранять

      // подготовить объект фильтра из тех полей которые сейчас выбрал пользователь
      let profileData = {};
      for (let key in this.filter.data){
        if (this.filter.data[key].show) profileData[key] = this.filter.data[key];
      };

      if (Object.keys(profileData).length === 0) return; // сохранять нечего... все поля скрыты .... пользователь просто олень

      // добавить новый профиль в массив профилей
      this.filterData.data.filter.profiles.push({name: this.profileName, data : profileData});
      if (this.$eventBus) this.$eventBus.$emit('changeFilterProfile', {action : 'add', grid : this.grid, name : this.profileName, data : profileData });

      // применить добавленный профиль
      this.selectProfile(profileData, this.filterData.data.filter.profiles.length - 1);

      this.profileName = '';
      this.isSave = false;
    },

    removeProfile: function(idx){
      // удалить профиль из массива профилей
      this.filterData.data.filter.profiles.splice(idx, 1);
      if (this.$eventBus) this.$eventBus.$emit('changeFilterProfile', {action : 'remove', grid : this.grid, idx : idx - 1});

      // применить дефолтовый профиль
      this.selectProfile(this.filterData.data.filter.profiles[0].data, 0);
    },

  },

  created(){
    // создать объект фильтра которым потом будем делаить штатную нормализацию фильтра грида
    Vue.set(this.filterData.data, 'filter', Object.assign({}, this.filter));

    // обнулить массив профилей (заодно делает работу очевидной и теряется связь с объектом фильтра у грида в самом приложении)
    this.filterData.data.filter.profiles = [];

    // на нулевом месте в массиве профилей всегда дефолтовый профиль
    this.filterData.data.filter.profiles.push( {name: 'По умолчанию', data : this.filter.data} );

    // если у пользователя были сохраненные профили то будут добавлены в массив профилей
    this.filter.profiles.forEach(el => { this.filterData.data.filter.profiles.push(el) });

    // сохранить имена полей которые видны у фильтра по умолчанию указаны в приложении у грида
    for (let key in this.filter.data) {
      if (this.filter.data[key].show) this.defaultFilds.push(key);
    }
  }
}
</script>

<style scoped>
 .profile-wrapper {
   width: 300px;
   margin-right: 20px
 }
 .change-fields-input{
   display: flex !important;
   flex: 1 100%;

   min-width: 160px;
   margin-top: 5px;
 }
 .change-fields-input-selected {
   background: #3bc8f5;
   color: white;
   border: #3bc8f5;
 }

 .change-profile-fields{
   white-space: pre-line;
   margin-top: 5px;
   display: flex !important;
   flex: 1 100%;
   padding-right: 10px;
   min-width: 160px;
 }
 .profile-button{
   display: flex !important;
   margin-top: 5px;
   flex: 1 1 40px;
   padding: 12px !important;
 }

</style>